import { Controller } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export default function isSelectWholeCol(controller: Controller, { node }: { node: Table }) {
  const tableSelection = controller.query('tableSelection');
  if (!tableSelection || tableSelection.key !== node.key) {
    return false;
  }
  const isSelectWholeTable = controller.query('isSelectWholeTable', {
    node,
  });
  return !isSelectWholeTable
    && tableSelection.startRowIndex === 0
    && tableSelection.endRowIndex === node.nodes.length - 1;
}
